@font-face {
  font-family: "PT Mono";
  font-weight: 400;
  font-style: normal;
  src: url("./../fonts/pt-mono-regular.woff2") format("woff2"), url("./../fonts/pt-mono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 400;
  font-style: normal;
  src: url("./../fonts/roboto-condensed-regular.woff2") format("woff2"), url("./../fonts/roboto-condensed-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-weight: 700;
  font-style: normal;
  src: url("./../fonts/roboto-condensed-bold.woff2") format("woff2"), url("./../fonts/roboto-condensed-bold.woff") format("woff"); }

html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  scroll-behavior: smooth; }

body {
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 16px;
  font-weight: 400; }

*,
*::after,
*::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

h2 {
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px; }
  @media (max-width: 1023px) {
    h2 {
      font-size: 32px;
      line-height: 140.62%; } }
  @media (max-width: 767px) {
    h2 {
      font-size: 28px; } }

img {
  display: block;
  max-width: 100%;
  height: auto; }

@media (max-width: 767px) {
  .logo-light {
    display: none; } }

.logo-mobile {
  display: none; }
  @media (max-width: 767px) {
    .logo-mobile {
      display: block; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  background-color: #1b2d37; }

.page-header__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding-top: 33px;
  padding-bottom: 33px; }
  @media (max-width: 1023px) {
    .page-header__container {
      padding-top: 27px;
      padding-bottom: 27px; } }
  @media (max-width: 767px) {
    .page-header__container {
      padding-top: 17px;
      padding-bottom: 17px; } }

.page-header a {
  display: block; }

.page-header__contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.page-header button {
  padding: 14px 26px 13px;
  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: inherit;
  border: 1px solid #ffffff;
  border-radius: 5px; }
  @media (max-width: 1023px) {
    .page-header button {
      display: none; } }

.contacts-list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0;
  margin: 0;
  list-style: none; }

.contacts-list a {
  display: block;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none; }

@media (max-width: 1023px) {
  .contacts-list__svg-email {
    width: 25px;
    height: 19px; } }

@media (max-width: 1023px) {
  .contacts-list__svg-phone {
    width: 24px;
    height: 24px; } }

@media (max-width: 1023px) {
  .contacts-list span {
    display: none; } }

.container {
  max-width: 1490px;
  margin-left: auto;
  margin-right: auto; }
  @media (max-width: 1023px) {
    .container {
      padding-left: 25px;
      padding-right: 25px; } }
  @media (max-width: 767px) {
    .container {
      padding-left: 15px;
      padding-right: 15px; } }

.promo {
  background-color: #1b2d37; }

.promo__container {
  padding: 194px 0 278px; }

.promo h1 {
  margin-top: 0;
