  font-family: "Roboto Condensed", "Courier New", monospace;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .promo h1 {
